#projects_midpage{
    width: 80%;
    margin-left: 10%;
    font-family: 'Lato', sans-serif;
    color: #222222;
}


@media screen and (max-width: 600px) {
    #projects_midpage{
        width: 90%;
        margin-left: 5%;
    }
}


.project_div{
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.projectfloatleft{
    float: left;
    width: 30%;
}

.projectfloatright{
    float: right;
    width: 70%;

}

.project_name {
    align-items: center;
    text-align: center;
    font-family: Pacifico;
    font-size: 23px;
}